
import { defineComponent } from "vue";
import HeaderItem, { HeaderI } from "./HeaderItem.vue";
import SearchBar1 from "@/components/pages/home/SearchBar.vue";
import { mapGetters } from "vuex";
import { logout } from "@/services/auth";
import store from "@/store";
import MenuDropDown, {
  DopDownItems,
} from "@/components/headlessui/MenuDropDown.vue";

export default defineComponent({
  name: "link-HeaderDesktop",
  components: {
    HeaderItem,
    SearchBar1,
    MenuDropDown,
  },
  data() {
    return {
      headerItems: [
        {
          name: "Communauté",
          link: "",
        },
        {
          name: "Artiste du mois",
          link: "",
        },
      ] as HeaderI[],
      pageScroll: false,
      showSearchBar: false,
      showProfileMenu: false,

      menuItems: [
        {
          label: "Mon profil",
          icon: "user",
          action: "redirect",
          link: "/profile",
        },
        // {
        //   label: "Mes favoris",
        //   icon: "heart",
        //   action: "redirect",
        //   link: "",
        // },
        // {
        //   label: "Paramètres de mon compte",
        //   icon: "setting-2",
        //   action: "redirect",
        //   link: "",
        // },
      ] as DopDownItems[],
      footerItems: [
        {
          label: "Déconnexion",
          icon: "logout",
          action: "event",
          link: "logout",
        },
      ] as DopDownItems[],

      dropDownItems: [
        {
          label: "Chanteur",
          action: "redirect",
          link: "/",
        },
        {
          label: "Musicien",
          action: "redirect",
          link: "/",
        },
        {
          label: "Beatmaker",
          action: "redirect",
          link: "/",
        },
        {
          label: "Clipper - Monteur",
          action: "redirect",
          link: "/",
        },
        {
          label: "Graphiste",
          action: "redirect",
          link: "/",
        },
      ] as DopDownItems[],
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    async logout() {
      await logout();
      store.dispatch("LOGOUT");
      this.$router.push("/login");
    },
    onScroll() {
      if (!window?.top) return;

      if (window?.top?.scrollY > window.innerHeight / 2 && !this.pageScroll) {
        this.pageScroll = true;
      } else if (window?.top?.scrollY < window.innerHeight / 2) {
        this.pageScroll = false;
      }
    },
  },
  computed: {
    ...mapGetters(["getUser", "getId"]),
  },
});
