
import { defineComponent } from "vue";

export default defineComponent({
  name: "link-SearchBar",
  emits: ["changeVersion"],
  props: {
    darkTheme: {
      type: Boolean,
      default: false,
    },
    inputSize: {
      type: String,
    },
  },
  computed: {
    inputDarkTheme(): string {
      return this.darkTheme ? "bg-white text-black" : "bg-black text-white";
    },
  },
});
