
import { PropType, defineComponent, ref } from "vue";
import {
  Combobox,
  ComboboxInput,
  ComboboxButton,
  ComboboxOptions,
  ComboboxOption,
  TransitionRoot,
} from "@headlessui/vue";

export default defineComponent({
  components: {
    Combobox,
    ComboboxInput,
    ComboboxButton,
    ComboboxOptions,
    ComboboxOption,
    TransitionRoot,
  },
  props: {
    data: {
      type: Array as PropType<any[]>,
      required: true,
    },
    modelValue: {
      type: String || Number,
      required: true,
    },
  },
  data() {
    return {
      selected: ref(this.data[0]),
      query: "",
    };
  },
  watch: {
    data: {
      handler() {
        this.selected = this.data[0];
        this.$emit("update:modelValue", this.data[0].id);
      },
      deep: true,
    },
  },
  computed: {
    filteredPeople(): any[] {
      return this.query === ""
        ? this.data
        : this.data.filter((person) =>
            person.name
              .toLowerCase()
              .replace(/\s+/g, "")
              .includes(this.query.toLowerCase().replace(/\s+/g, ""))
          );
    },
  },
});
